.user-profile-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.user-profile-modal {
  background: #1a1a1a;
  padding: 30px;
  border-radius: 15px;
  width: 90%;
  max-width: 400px;
  text-align: center;
  color: white;
  border: 1px solid #333;
  box-shadow: 0 10px 25px rgba(0,0,0,0.5);
}

.profile-pic-large {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-bottom: 15px;
  border: 3px solid #007bff;
}

.profile-header h3 {
  cursor: pointer;
  margin: 10px 0;
}

.profile-header h3:hover {
  color: #007bff;
}

.edit-name-container {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin: 10px 0;
}

.edit-name-input {
  padding: 5px;
  border-radius: 4px;
  border: 1px solid #444;
  background: #333;
  color: white;
}

.profile-actions {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.btn-logout {
  background: #dc3545;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
}

.btn-close {
  background: transparent;
  color: #aaa;
  border: 1px solid #444;
  padding: 8px;
  border-radius: 5px;
  cursor: pointer;
}
